import { Component, OnInit } from '@angular/core';
import { LivreurUberService } from '../livreur-uber.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(private service : LivreurUberService) { }

  nombreGateauxPanier(){
  this.nombreProduits = this.service.livrerLaCommande().length;
  }

  nombreProduits:number;
  intervalle:any;
  

  ngOnInit(): void {

    this.nombreGateauxPanier();
    this.intervalle = setInterval( () => {
      this.nombreGateauxPanier()
    }, 500);
  };

}
