import { Component, Input,  OnInit } from '@angular/core';
import { LivreurUberService } from '../livreur-uber.service';

@Component({
  selector: 'app-panier',
  templateUrl: './panier.component.html',
  styleUrls: ['./panier.component.css']
})
export class PanierComponent implements OnInit {

  @Input() lePanier;

  constructor(private unService : LivreurUberService) { }

  ngOnInit(): void {
    this.lePanier = this.unService.livrerLaCommande();
  }

  totalPrix(){
    let total = 0;

    this.lePanier.forEach(lesGateaux => total += lesGateaux.prix);

    return total;
  }

  supprPanier(quelGateau:Object){

    let indexDuGateau = this.lePanier.indexOf(quelGateau);
    this.lePanier.splice(indexDuGateau , 1);
  }

}
