import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LivreurUberService {

  sacADosUber = Array();

  allerChercherLaCommande(laCommande){
    this.sacADosUber = laCommande;
  }

  livrerLaCommande(){
    return this.sacADosUber;
  }

  constructor() { }
}
